import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';

import { DateField, Loader, SelectField, TextField } from '../components';

import { OwnerUpdate, OwnerQuery } from '../api';
import { useSnackbar } from '../hooks';
import { UpdateOwner, LangPriorities, LangPriority } from '../utils/dataTypes';
import { cleanObject } from '../utils/queryHelpers';
import FileList from '../components/filesList';

const validationSchema = yup.object({
  fullName: yup.string().required('First name/Last name is required'),
  birthDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Birth Date is required'),
  citizenship: yup.string().required('Citizenship is required'),
  languagePriority: yup
    .string()
    .oneOf(LangPriorities)
    .required('Language Priorities is required'),
  hiredBy: yup.string().required('Hired By is required'),
  hireDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Hire Date is required'),
  snn: yup.string().required('Snn is required'),
  company: yup.string(),
  insurancePolicy: yup.string().required('Insurance Policy is required'),
  insurancePolicyExp: yup
    .date()
    .typeError('Enter correct date')
    .required('Insurance Policy Exp is required'),
  address: yup.string().required('Home address is required'),
  phone: yup.string().required('Phone is required'),
  phone2: yup.string(),
  email: yup
    .string()
    .email('Enter a valid email')
    .required('Email is required'),
  emergencyContactName: yup.string(),
  emergencyContactRel: yup.string(),
  emergencyContactPhone: yup.string(),
  notes: yup.string(),
});

const initialValues = {
  id: '',
  fullName: undefined,
  birthDate: undefined,
  citizenship: undefined,
  languagePriority: 'EN' as LangPriority,
  hiredBy: undefined,
  hireDate: undefined,
  snn: undefined,
  company: undefined,
  insurancePolicy: undefined,
  insurancePolicyExp: undefined,
  address: undefined,
  phone: undefined,
  phone2: undefined,
  email: undefined,
  emergencyContactName: undefined,
  emergencyContactRel: undefined,
  emergencyContactPhone: undefined,
  notes: undefined,
};

export default function EditOwnerForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['owner', pathname.split('/')[3]],
    queryFn: OwnerQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { mutate, isLoading } = useMutation(OwnerUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['owners']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateOwner>({
    initialValues: {
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="topControls" sx={{ p: 1, paddingBottom: 0 }}>
              <TextField
                fieldName="fullName"
                label="First name/Last name"
                formik={formik}
              />
            </Box>
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box
                key="left"
                sx={{ p: 1, paddingTop: 0, paddingBottom: 0, width: '50%' }}
              >
                <DateField
                  fieldName="birthDate"
                  label="Birth Date"
                  formik={formik}
                />
                <SelectField
                  fieldName="languagePriority"
                  label="Language Priority"
                  items={LangPriorities}
                  formik={formik}
                />
                <TextField
                  fieldName="hiredBy"
                  label="Hired By"
                  formik={formik}
                />
                <TextField
                  fieldName="company"
                  label="Company"
                  formik={formik}
                />
                <TextField
                  fieldName="address"
                  label="Home address"
                  formik={formik}
                />
                <TextField fieldName="email" label="Email" formik={formik} />
                <TextField
                  fieldName="emergencyContactName"
                  label="Emergency Contact Name"
                  formik={formik}
                />
                <TextField
                  fieldName="emergencyContactPhone"
                  label="Emergency Contact Phone"
                  formik={formik}
                />
                <TextField
                  fieldName="appLogin"
                  label="Mobile App Login"
                  formik={formik}
                />
              </Box>
              <Box
                key="right"
                sx={{ p: 1, paddingTop: 0, paddingBottom: 0, width: '50%' }}
              >
                <TextField
                  fieldName="citizenship"
                  label="Citizenship"
                  formik={formik}
                />
                <TextField fieldName="snn" label="SSN#" formik={formik} />
                <DateField
                  fieldName="hireDate"
                  label="Hire Date"
                  formik={formik}
                />
                <TextField
                  fieldName="insurancePolicy"
                  label="Insurance Policy"
                  formik={formik}
                />
                <DateField
                  fieldName="insurancePolicyExp"
                  label="Insurance Policy Exp"
                  formik={formik}
                />
                <TextField fieldName="phone" label="Phone" formik={formik} />
                <TextField fieldName="phone2" label="Phone2" formik={formik} />
                <TextField
                  fieldName="emergencyContactRel"
                  label="Emergency Contact Rel"
                  formik={formik}
                />
                <TextField
                  fieldName="appPass"
                  label="Mobile App Pass"
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ p: 1, paddingBottom: 0, paddingTop: 0 }}
            >
              <TextField
                fieldName="notes"
                label="Notes"
                multiline
                formik={formik}
              />
              <FileList objectId={data?.id} objectType="Person" />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
