export enum StopType {
  PickUp = 'PickUp',
  Delivery = 'Delivery',
}

export enum TimeFrameType {
  FCFS = 'FCFS',
  APPT = 'APPT',
  ASAP = 'ASAP',
  Direct = 'Direct',
}

export type TimeFrameFCFS = {
  type: TimeFrameType.FCFS;
  from: Date | null;
  to: Date | null;
};

export type TimeFrameAPPT = {
  type: TimeFrameType.APPT;
  at: Date | null;
};

export type TimeFrameASAP = {
  type: TimeFrameType.ASAP;
  at: Date | null;
};

export type TimeFrameDirect = {
  type: TimeFrameType.Direct;
  at: Date | null;
};

export interface StopPickUp {
  type: StopType.PickUp;
  timeFrame: TimeFrameFCFS | TimeFrameAPPT | TimeFrameASAP;
  facility: Facility | null;
  freightList: Freight[];
  addInfo?: string;
}

export interface StopDelivery {
  type: StopType.Delivery;
  timeFrame: TimeFrameFCFS | TimeFrameAPPT | TimeFrameDirect;
  facility: Facility | null;
  addInfo?: string;
}

export interface Freight {
  pieces: number | null;
  unitOfWeight: UnitOfWeight;
  weight: number | null;
  unitOfLength: UnitOfLength;
  length: number | null;
}

export interface Push {
  readonly id: string;
  readonly state: PushState;
  readonly to: Person;
  readonly title?: string;
  readonly subtitle?: string;
  readonly body?: string;
  readonly data?: Record<string, any>;
  readonly badge?: number;
  readonly sendResult?: Record<string, any>;
  readonly receiptResult?: Record<string, any>;
  readonly createdAt: Date;
  readonly updatedAt: Date;
}

export type To = {
  readonly to: Person | User;
  readonly toType: EmailToType;
};

export interface Email {
  readonly id: string;
  readonly state: EmailState;
  readonly from: string;
  readonly to: To[];
  readonly subject: string;
  readonly text: string;
  readonly html?: string;
  readonly sendResult?: Record<string, any>;
  readonly createdAt: Date;
  readonly updatedAt: Date;
}
